.BtnWraper {
  display: flex;
}

.PaginationBtn {
  cursor: pointer;
  display: block;
  width: 196px;
  margin: 0 auto;
  padding: 14px 56px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  color: #373737;
  background-color: #ebd8ff;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 10.3108px;
  border: none;
  &:disabled {
    background-color: #aa98bd;
  }
  &:hover {
    background-color: #5cd3a8;
  }
}

.CurrentPage {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  background-color: #ebd8ff;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 50%;
}
